import React, { useState, useEffect } from 'react';
import { Button, Table } from 'reactstrap';
import MDBNavBar from "./MDBNavBar"
import { promptIDs} from "./analyzeEvents"
import { getAssessmentIcon, getColor, getFeedback, getTimestamp, saveAnalysisLog} from "./debriefingUtils"



const DisplayBaseDebriefing = ({log, actions, scenario, context}) => {
    const [showCommentColumn, setShowCommentColumn] = useState(false);
    // eslint-disable-next-line
    //const [events, setEvents] = useState(log.events);
    const [formattedActions, setFormattedActions] = useState(undefined);

// const Debriefing = ({ log, metaData, criticalActions, associations }) => {
    /* const toggleCommentColumn = () => {
        setShowCommentColumn(!showCommentColumn);
    } */
    

    const saveAnalysisLogLocally = () => {
        saveAnalysisLog(log,formattedActions)
    }

    const toggleComments = () => {
        setShowCommentColumn(!showCommentColumn)
    }

    const formatAnswerGiven = (entry) => {
        let answers = []
        let answer = []
        for (const [prompt, value] of Object.entries(entry.answerDetails)){
            let answerString = String(value.givenAnswer)
            answer = answerString.split(":")
            if (answer.length === 2){     
                answers.push({ID: answer[0], answer: answer[1]})
            }
            else {answers.push({ID: promptIDs[prompt], answer: value.givenAnswer})}
        }
        
        return (
            <>
            {answers.map((entry) => (
            <p><b>{entry.ID}:</b> {entry.answer}</p>))}
            </>)
    }
    

    const dispHeaderRow = (entry) => {
        return (
            <>
                {entry.id ===  "assessmentHeader"  || entry.id === "assessmentSubHeader"
                    ? <td colSpan="4" className={getColor(entry)}>
                        <b>{entry.label}</b>
                    </td>
                    : <td colSpan="10" className={"black"} >
                        <div><em>
                        &nbsp;&nbsp;&nbsp;Intervention Group: {entry.probLabel}</em></div>
                    </td>}
            </>
        )
    }
    
    function getLabel(entry) {
        return(
            <>
            {entry.type === "obtain-vital-sign"
                ? vitalLabel(entry)
                : regularLabel(entry)
            }
            </>
    
        )
    }
    
    function getStatusLabel(entry){
        let metaData = scenario.intvMetaData
        let metaEntry = metaData.find(me => (entry.vital === me.id))
        return(<>{metaEntry.label}</>)
    }
    
    function vitalLabel(entry){
        return(
            <>
            {entry.vitalType === "intervention-status"
                ? <><b>Requested intervention status for:</b> {getStatusLabel(entry)}, <td className="text-muted"><p><b>Found:</b>&nbsp;<i>{entry.value}</i></p></td></>
                : <><b>Requested vital:</b> {entry.vital}, <td className="text-muted"><p><b>Found:</b>&nbsp;<i>{entry.value}</i></p></td></>
            }
            </>
        )
    }
    
    function regularLabel(entry) {
        return(
            <>
            {entry.subPhase && entry.type !== "intervention"
                ? <><b>{entry.subPhase}:</b> {entry.actionDescription}.  {findingsData(entry)}</>
                : <>{phaseLabel(entry)}</>
        }
            </>
        )
    }
    
    function phaseLabel(entry){
        return(
            <>
            {entry.type === "intervention"
                ? <><b>Intervention:</b> {entry.actionDescription}. {answerData(entry)} </>
                : <>{entry.actionDescription}.  {findingsData(entry)}</>
                }
            </>
        )
    }
        
    function findingsData(entry){
        return(
            <>
            {entry.finding
            ?<td className="text-muted"><p><b>Findings:</b> <i>{entry.finding}</i></p></td>
            :<></>
        }
            </>
        )
    
    }

    function answerData(entry){
        return(
            <>
            {entry.answerDetails
             ?<td className="text-muted">{formatAnswerGiven(entry)}</td>
             :<></>
            }
            </>
        )
    }
            
    
     function saveComment(val) {
        let orig = val.target.defaultValue;
        let comment = val.target.value;
        let entryID = val.target.id;
        if (orig !== comment){
            let entry = formattedActions.find(entry => (entry.id === entryID))
            // if (entry) { entry.comment = comment }}
            const newEntry = Object.assign({}, entry, {comment: comment})
            updateFormattedAction(newEntry)
        }
    }
    
    const dispDataRow = (entry) => {
        return (
            <>
                <td>{entry.numericalID}</td>
                <td>{getTimestamp(entry)}</td>
                <td className={getColor(entry)}>
                    <span className="material-icons-outlined">{getAssessmentIcon(entry)}</span>
                    {getLabel(entry)}
                </td>
                <td>{getFeedback(entry)}</td>

                {showCommentColumn && <td><textarea rows="2" cols="50" defaultValue={entry.comment} onBlur={saveComment} id={`${entry.id}`}/></td>}
            </>
        )
    }

    
    const updateFormattedAction = (record) => {
        const index = formattedActions.findIndex(rec => rec.id === record.id)
        setFormattedActions([
            ...formattedActions.slice(0, index),
            record,
            ...formattedActions.slice(index+1)
        ])
    }    


    async function displayIt () {
        
        
        if (context === "review"){
            //console.log("displaying for review")
            //setFormattedActions(actions)
            setShowCommentColumn(true)        
        }
        else {
            //console.log("displaying for non-review")
            //setFormattedActions(actions)
            setShowCommentColumn(false)
              }

        setFormattedActions(actions)
    
        
    }

    
   


// eslint-disable-next-line
useEffect(
    () => {
        displayIt()
        // eslint-disable-next-line
    }, [] // empty list means this code will only be run on initial render
)



if (undefined === formattedActions) {
    return (<h3>processing</h3>)
}

return (
        <>
            {context === "review"
                ? <><MDBNavBar
                    title={`Review of: ${log.label}`}
                    leftNav={
                        <Button
                            color="primary"
                            onClick={saveAnalysisLogLocally}>Save Debriefing Log
                        </Button>}
                    rightNav={
                        <Button
                            color="primary"
                            onClick={toggleComments}>Toggle Comments
                        </Button>}
                     /></>
                : <><MDBNavBar
                    title={`Analysis for: ${log.label}`}
                    /></>
            }
            <form>
            <div style={{ overflowY: "auto", maxHeight: "700px" }}>
                <Table borderless size="sm">
                    <thead>
                        <tr>
                            <th className="table-header"> ID </th>
                            <th className="table-header"> Timestamp </th>
                            <th className="table-header"> Action Description </th>
                            <th className="table-header"> Feedback </th>
                            {showCommentColumn && <th className="table-header"> Comments </th>}
                        </tr>
                    </thead>
                    <tbody className="scrolling">
                        {formattedActions.map((currentEntry, index) => (
                            <tr key={index}>
                                {currentEntry.type === "header"
                                    ? dispHeaderRow(currentEntry)
                                    : dispDataRow(currentEntry)
                                }
                            </tr>
                        ))}
                    </tbody>
                </Table>
            </div>
        </form>
        </>

    )
}

export {saveAnalysisLog}  
export default DisplayBaseDebriefing
